.banner{
    background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/home-header.png?raw=true);
    h1{
    color: #650300;
    font-size: 64px;
    font-weight: bold;
    text-align: right;
    margin-bottom: 4px;
    span{
      font-size: 36px;
      font-weight: normal;
    }
    }
  }
  .container{
    max-width: 1296px;
    margin:0 auto;
    padding: 0px 12px;
    &-s{
      max-width: 636px;
      margin: 0 auto;
    }
  }
  .buybtn{
    background-color: $primarycolor;
    color:#FFFFFF;
    border: 0px;
    font-size: 20px;
    font-weight: bold;
    padding: 6px 12px;
    &:hover{
      background-color: #650300;
    }
  }
  .feature{
    background-color: $secondarycolor;
  }
  .title{
    color: #650300;
    font-size: 48px;
    line-height: 72px;
    font-weight: bold;
    text-align: center;
    &-white{
      color:white;
    }
    &-ita{
      color: #AA0601;
      font-style: italic;
      font-weight: 900;
    }
  }
  .icon{
    color: #650300;
    li{
      max-width: calc((100% - 72px) / 4);
      h3{
        font-size: 24px;
        line-height: 36px;
        font-weight: bold;
        text-align: center;
        span{
          display: block;
          font-size: 84px;
          font-weight: bold;
        }
      }
      p{
        color: #000;
      }
    }
    }
    .classic{
      li{
        max-width: calc((100% - 48px) / 3);
        &:hover{
          transform: scale(1.1);
          h3{
            color: #650300;
          }
        }
      }
      }
    .section-4{
      background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/index-bg.png?raw=true);
    }
    .cobrand{
      h3{
        background-color: #fff;
      }
      li{
        &:hover{
            img{
                transform: scale(1.5);
            }
            h3{
                opacity: 80%;
            }
          } 
      }
    }
    .brand{
      overflow: hidden;
      img{
        transform:scale(1,1);
        transition: all .3s ease-out;
      }
    }
    .recommend{
      li{
        width: 23%;
        border-radius: 4px;
        border: 1px solid #DEE2E6;
        box-shadow: 0px 2px 6px #00000029;
        span{
          color: #707070;
          font-size: 14px;
          line-height: 21px;
        }
      }
    }
    .contactform{
      background-color: $secondarycolor;
      p{
        font-size: 20px;
        line-height: 30px;
      }
    }
    
    .form{
      width: 100%;
      padding: 6px 12px;
      border: 1px solid #000;
      word-wrap: normal;
    }
    .submit{
      &-btn{
        background-color: $primarycolor;
        color: #FFF;
        display: inline-block;
        border: 0px;
        padding: 16px 176px;
        font-weight: bold;
        &:hover{
          background-color: #650300;
        }
      }
    }