.header{
    background-color: $primarycolor;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .logo{
    a{
      display: block;
      width: 80px;
      height: 49px;
      background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/logo-white.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  .nav{
      li{
        font-size: 20px;
      }
      a{
        display: inline-block;
        padding: 25px;
        margin-left: 14px;
        &:hover{
          opacity: 70%;
        }
    }
  }
  // header & footer
  .footer{
    background-color: $primarycolor;
    color: #FFF;
    a{
      color: #FFFFFF;
      text-decoration: none;
      &:hover{
        opacity: 70%;
      }
    } 
    &-contact{
      border-bottom: 1px solid #FFF;
      li{
        font-size: 36px;
        line-height: 54px;
        span{
          font-size: 48px;
        }
      }
    }
    &-logo{
      img{
        width: 48px;
        height: 48px;
      }
    }
    }
    //layout